	methods: {
		gotopriceControl(item, index) { //采购路由
			if (index == 0 || index == 4) {
				uni.switchTab({
					url: item.url
				})
			} else {
				uni.navigateTo({
					url: item.url
				})
			}
		},
		gotoSale(item, index) { //销售路由	
			if (index == 0 || index == 3) {
				uni.switchTab({
					url: item.url
				})
			} else {
				uni.navigateTo({
					url: item.url
				})
			}
		},
		gotoAccount(item) { //账户路由
			uni.navigateTo({
				url: item.url
			})

		},
		//判断是否登录状态
		...mapMutations(['logout']),
		bindLogin() {
			if (this.hasLogin) {
				this.logout();
				uni.reLaunch({
					url: '/pages/user/login'
				})
			}
		}
	},
	onLoad() {
		this.usertype = this.userInfo.user_type  //登录type
		console.log(this.userInfo);
	}
